import React from "react";
import styled from "styled-components";
import MenuIcon from "@material-ui/icons/Menu";
import CloseIcon from "@material-ui/icons/Close";
import { useState } from "react";
import { useSelector } from "react-redux";
import { selectCars } from "../features/car/carSlice";

const Header = () => {
  const [sideNavStatus, setSideNavStatus] = useState(false);
  const cars = useSelector(selectCars);
  console.log(cars);
  return (
    <Container>
      <a href="#">
        <img src="/img/logo.svg" alt="Logo" />
      </a>
      <Menu>
        {cars &&
          cars.map((car, index) => (
            <a key={index} href="#">
              {car}
            </a>
          ))}

        <a href="#" className="test">
          Model 3
        </a>

        <a href="#">Model X</a>

        <a href="#">Model Y</a>
      </Menu>

      <RightMenu>
        <a href="#">Shop</a>
        <a href="#">Tesla Account</a>
        <CustomMenu onClick={() => setSideNavStatus(true)} />
      </RightMenu>
      <SideNav show={sideNavStatus}>
        <NewWrapp>
          <CustomClosed onClick={() => setSideNavStatus(false)} />
        </NewWrapp>
        {cars &&
          cars.map((car, index) => (
            <li key={index}>
              <a href="#">{car}</a>
            </li>
          ))}
        <li>
          <a href="#">Inventory</a>
        </li>
        <li>
          <a href="#">Trade-In</a>
        </li>
        <li>
          <a href="#">Page</a>
        </li>
        <li>
          <a href="#">Page</a>
        </li>
        <li>
          <a href="#">Page</a>
        </li>
        <li>
          <a href="#">Page</a>
        </li>
        <li>
          <a href="#">Page</a>
        </li>
        <li>
          <a href="#">Page</a>
        </li>
      </SideNav>
    </Container>
  );
};

export default Header;

const Container = styled.div`
  min-height: 60px;
  position: fixed;
  padding: 0 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  top: 0;
  right: 0;
  left: 0;
  z-index: 1;
`;
const Menu = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 1;

  a {
    font-weight: 600;
    text-transform: uppercase;
    margin: 0 10px;
    flex-wrap: nowrap;
  }
  /*.test{
    color: red!important;
  } -- working*/
  @media (max-width: 768px) {
    display: none;
  }
`;
const RightMenu = styled.div`
  display: flex;
  align-items: center;

  a {
    font-weight: 600;
    text-transform: uppercase;
    margin-right: 10px;
  }
`;
const CustomMenu = styled(MenuIcon)`
  cursor: pointer;
`;
const SideNav = styled.div`
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  background: #fff;
  width: 300px;
  z-index: 99;
  list-style: none;
  padding: 20px;
  display: flex;
  flex-direction: column;
  /*justify-content: space-evenly;*/
  transform: ${(props) => (props.show ? "translateX(0)" : "translateX(100%)")};
  transition: all 0.5s;
  li {
    padding: 15px 0;
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  }
`;

const NewWrapp = styled.div`
  display: flex;
  justify-content: flex-end;
`;

const CustomClosed = styled(CloseIcon)`
  /* position: absolute;
  right: 1em;*/
  cursor: pointer;
`;



